.Login {
  min-width: 280px;
  text-align: left;
  padding: 20px 20px 10px 20px;
  background-color: #535861A8;
  border-radius: 9px;
}

input {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  width: 100%;
  padding: 6px;
  margin-bottom: 8px;
  border-radius: 4px;
  outline: none;
  border: none;
  font-size: 1.05em;
}

#email {
  margin-bottom: 32px;
}

#voucher {
  margin-bottom: 18px;
}

input.error {
  background-color: #BB95A6;
}

.button {
  -webkit-appearance: none;
  width: 100%;
  background-color: #5CABF7;
  border: 0px;
  border-radius: 4px;
  padding: 12px;
  font-weight: 700;
  color: #FFF;
  cursor: pointer;
  text-decoration: none;
  outline: none;
}

.button:disabled {
  background-color: #7E8491;
}

.button:hover {
  border: 0px;
  background-color: #57A1E9;
}

.button:disabled:hover {
  cursor: default;
  background-color: #7E8491;
}

.button:focus {
  background-color: #57A1E9;
}

